import "./MeetupForm.module.css";
import Card from "../ui/Card";
import classes from "./MeetupForm.module.css";
import { useRef } from 'react';

const MeetupForm = (props) => {
    const titleInputRef = useRef();
    const imgInputRef = useRef();
    const addressInputRef = useRef();
    const descrInputRef = useRef();

    function submitHandler(event) {
        //prevent the browser default
        event.preventDefault();

        const enteredTitle = titleInputRef.current.value;
        const enteredImgLink = imgInputRef.current.value;
        const enteredAddress = addressInputRef.current.value;
        const enteredDescr = descrInputRef.current.value;

        const meetupData = {
            title: enteredTitle,
            image: enteredImgLink,
            address: enteredAddress,
            description: enteredDescr
        };

        props.onAddMeetup(meetupData);
    }

    return (
        <Card>
            <form className={classes.form} onSubmit={submitHandler}>
                <div className={classes.control}>
                    <label htmlFor='title'>Meetup Title</label>
                    <input type='text' required id='title' ref={titleInputRef} />
                </div>
                <div className={classes.control}>
                    <label htmlFor='image'>Image</label>
                    <input type='url' required id='img' ref={imgInputRef}/>
                </div>
                <div className={classes.control}>
                    <label htmlFor='address'>Adress</label>
                    <input type='text' required id='address' ref={addressInputRef}/>
                </div>
                <div className={classes.control}>
                    <label htmlFor='image'>Description</label>
                    <textarea required id='description' rows='5' ref={descrInputRef}/>
                </div>
                <div className={classes.actions}>
                    <button>Add Meetup</button>
                </div>
            </form>
        </Card>
    );
};

export default MeetupForm;
