import Card from "../ui/Card";
import classes from "./MeetupCard.module.css";
import { useContext } from "react";
import FavoritesContext from "../../store/favorites-context";

const MeetupCard = ({ id, title, image, adress, description }) => {
    const favCtx = useContext(FavoritesContext);

    const isFavorite = favCtx.isFavorite(id);

    function toggleFavorite() {
        if (isFavorite) {
            favCtx.removeFavorite(id);
        } else {
            favCtx.addFavorite({
                id: id,
                title: title,
                image: image,
                address: adress,
                description: description,
            });
        }
    }

    return (
        <li className={classes.item}>
            <Card>
                <div className={classes.image}>
                    <img src={image} alt="city"></img>
                </div>
                <div className={classes.content}>
                    <h3>{title}</h3>
                    <adress>{adress}</adress>
                    <p>{description}</p>
                </div>
                <div className={classes.actions}>
                    <button onClick={toggleFavorite}>{isFavorite ? 'Remove from Favorites' : 'To Favorites'}</button>
                </div>
            </Card>
        </li>
    );
};

export default MeetupCard;
